// @flow

import debug from 'debug'
import * as R from 'ramda'

import { isNilOrEmpty } from './ramda-extra'

type CSSModule = {
  [key: string]: string,
}

type SimpleModifier = string | Symbol | null | typeof undefined

// flowlint-next-line unclear-type:off
type Modifier = SimpleModifier | Array<SimpleModifier> | Object

type ReduceAcc = {
  globals: Array<string>,
  locals: Array<string>,
}

const log = debug('ein:common:style-set')

const isSymbol = (value: mixed): boolean =>
  R.is(Symbol, value) || R.type(value) === 'Symbol'

const symbolToString = R.pipe(R.toString, R.slice('Symbol('.length, -1))
const extractTruthyKeys = R.pipe(R.filter(v => !!v), R.keys)

const isBlank = R.cond([
  [R.is(String), s => R.isEmpty(s.trim())],
  [R.T, isNilOrEmpty],
])

const rejectBlank = R.reject(isBlank)

class StyleSet {
  blockName: string
  styles: CSSModule

  static flattenClasses(classes: Array<Modifier>): Array<string> {
    let rv: Array<string> = []

    for (const name of classes) {
      if (typeof name === 'string') {
        rv.push(name)
      } else if (isSymbol(name)) {
        rv.push(symbolToString(name))
      } else if (Array.isArray(name)) {
        const xs = name.map(s => (isSymbol(s) ? symbolToString(s) : String(s)))
        rv = R.concat(rv, xs)
      } else if (R.is(Object, name) && !!name) {
        rv = R.concat(rv, extractTruthyKeys(name))
      }
    }

    return rv
  }

  static join(...args: Array<Modifier>): string {
    return R.pipe(StyleSet.flattenClasses, rejectBlank, R.join(' '))(args)
  }

  constructor(blockName: string, styles: CSSModule) {
    this.blockName = blockName
    this.styles = styles
  }

  block(...args: Array<Modifier>): string {
    return this._join({ args, baseName: this.blockName })
  }

  elem(name: string, ...args: Array<Modifier>): string {
    return this._join({ args, baseName: `${this.blockName}__${name}` })
  }

  _lookup(name) {
    if (!(name in this.styles)) {
      log('unknown class name:', name)
    }
    return this.styles[name] || ''
  }

  _getReducer(baseName) {
    const prefix = baseName.length > 0 ? `${baseName}--` : ''

    return (acc: ReduceAcc, arg: string) => {
      const name = `${prefix}${arg}`

      if (arg.startsWith('!')) {
        acc.globals.push(arg.slice(1))
      } else {
        acc.locals.push(this._lookup(name))
      }

      return acc
    }
  }

  _join({ args = [], baseName = '' }) {
    const { globals, locals } = R.pipe(
      StyleSet.flattenClasses,
      rejectBlank,
      R.reduce(this._getReducer(baseName), {
        globals: [],
        locals: [this._lookup(baseName)],
      })
    )(args)

    return rejectBlank(globals.concat(locals)).join(' ')
  }
}

export default StyleSet
